"use client"
import type React from "react"
import { useState, useRef, useCallback } from "react"
import Image from "next/image"
import { Upload, Loader2, AlertTriangle, Info, CheckCircle, AlertCircle, Camera } from "lucide-react"
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Progress } from "@/components/ui/progress"
import { type AnalysisData } from "@/types/analysis"

interface AnalysisResult extends AnalysisData {
  id: string
  date: string
}

const severityColors: Record<string, string> = {
  "No DR": "bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-100",
  "Mild DR": "bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-100",
  "Moderate DR": "bg-orange-100 text-orange-800 dark:bg-orange-900 dark:text-orange-100",
  "Severe DR": "bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-100",
  "Proliferative DR": "bg-red-200 text-red-900 dark:bg-red-950 dark:text-red-100",
}

const CONFIDENCE_THRESHOLDS = {
  HIGH: 70,
  MODERATE: 50,
  LOW: 30,
}

const getConfidenceLevel = (confidence: number): { color: string; message: string; icon: React.ReactNode } => {
  if (confidence >= CONFIDENCE_THRESHOLDS.HIGH)
    return {
      color: "text-green-600 dark:text-green-400",
      message: "High confidence prediction",
      icon: <CheckCircle className="w-5 h-5" />,
    }
  if (confidence >= CONFIDENCE_THRESHOLDS.MODERATE)
    return {
      color: "text-yellow-600 dark:text-yellow-400",
      message: "Moderate confidence - consider second opinion",
      icon: <AlertCircle className="w-5 h-5" />,
    }
  return {
    color: "text-red-600 dark:text-red-400",
    message: "Low confidence - results may not be reliable",
    icon: <AlertTriangle className="w-5 h-5" />,
  }
}

const MAX_FILE_SIZE = 10 * 1024 * 1024 // 10MB
const ALLOWED_TYPES = ["image/jpeg", "image/png"]

const API_URL = process.env.NEXT_PUBLIC_PYTHON_API_URL || "http://localhost:8000"

const Dashboard: React.FC = () => {
  const [selectedImage, setSelectedImage] = useState<string | null>(null)
  const [analyzing, setAnalyzing] = useState(false)
  const [uploading, setUploading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [analysisResult, setAnalysisResult] = useState<AnalysisData | null>(null)
  const [analysisHistory, setAnalysisHistory] = useState<AnalysisResult[]>([])
  const [isCameraOpen, setIsCameraOpen] = useState(false)
  const [isCameraReady, setIsCameraReady] = useState(false)
  const [isCapturing, setIsCapturing] = useState(false)
  const [cameraError, setCameraError] = useState<string | null>(null)
  const [cameraMode] = useState<'environment' | 'user'>('environment')

  const fileInputRef = useRef<HTMLInputElement>(null)
  const videoRef = useRef<HTMLVideoElement>(null)
  const streamRef = useRef<MediaStream | null>(null)

  const handleImageSelect = useCallback(async (file: File) => {
    if (analyzing) return

    if (file.size > MAX_FILE_SIZE) {
      setError('File size must be less than 10MB')
      return
    }

    if (!ALLOWED_TYPES.includes(file.type)) {
      setError('Please upload a JPEG or PNG image')
      return
    }

    setAnalyzing(true)
    setError(null)

    try {
      const formData = new FormData()
      formData.append('file', file)

      const response = await fetch(`${API_URL}/predict`, {
        method: 'POST',
        body: formData,
      })

      if (!response.ok) {
        throw new Error(`Analysis failed: ${response.statusText}`)
      }

      const result = await response.json()

      if (!result.success || !result.data) {
        throw new Error(result.error || 'Analysis failed')
      }

      const analysisData = {
        id: result.request_id || Date.now().toString(),
        date: new Date().toISOString(),
        ...result.data,
      }

      setAnalysisResult(result.data)
      setAnalysisHistory(prev => [analysisData, ...prev])
      setSelectedImage(URL.createObjectURL(file))
      
    } catch (err) {
      console.error('Analysis error:', err)
      setError(err instanceof Error ? err.message : 'An unexpected error occurred')
      setAnalysisResult(null)
    } finally {
      setAnalyzing(false)
      setUploading(false)
    }
  }, [])

  const formatConfidence = useCallback((confidence: number): string => {
    return `${confidence.toFixed(1)}%`
  }, [])

  const handleDrop = useCallback((e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault()
    const file = e.dataTransfer.files[0]
    if (file) handleImageSelect(file)
  }, [handleImageSelect])

  const handleFileChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (!file) {
      setError('No file selected')
      return
    }
    handleImageSelect(file)
  }, [handleImageSelect])

  const stopCamera = useCallback(() => {
    if (streamRef.current) {
      streamRef.current.getTracks().forEach((track: MediaStreamTrack) => track.stop())
      streamRef.current = null
    }
    if (videoRef.current) {
      videoRef.current.srcObject = null
    }
    setIsCameraOpen(false)
    setIsCameraReady(false)
  }, [])

  const startCamera = useCallback(async () => {
    try {
      stopCamera()

      const constraints: MediaStreamConstraints = {
        video: {
          facingMode: cameraMode,
          width: { ideal: 1920 },
          height: { ideal: 1080 },
        },
        audio: false
      }

      const stream = await navigator.mediaDevices.getUserMedia(constraints)
      
      if (videoRef.current) {
        videoRef.current.srcObject = stream
        streamRef.current = stream
        
        await new Promise<void>((resolve) => {
          if (videoRef.current) {
            videoRef.current.onloadedmetadata = () => resolve()
          }
        })

        await videoRef.current.play()
        setIsCameraOpen(true)
        setIsCameraReady(true)
        setCameraError(null)
      }
    } catch (err) {
      console.error('Camera access error:', err)
      setCameraError('Could not access camera. Please check permissions and try again.')
      setIsCameraOpen(false)
      setIsCameraReady(false)
    }
  }, [stopCamera])

  const captureImage = useCallback(async () => {
    if (!videoRef.current || !isCameraReady) {
      setCameraError('Camera is not ready')
      return
    }

    setIsCapturing(true)
    setError(null)

    try {
      const video = videoRef.current
      const canvas = document.createElement('canvas')
      canvas.width = video.videoWidth
      canvas.height = video.videoHeight

      const ctx = canvas.getContext('2d')
      if (!ctx) throw new Error('Could not get canvas context')

      ctx.drawImage(video, 0, 0)

      const blob = await new Promise<Blob>((resolve, reject) => {
        canvas.toBlob(
          (blob) => {
            if (blob) resolve(blob)
            else reject(new Error('Failed to capture image'))
          },
          'image/jpeg',
          0.95
        )
      })

      const file = new File([blob], 'retina-capture.jpg', {
        type: 'image/jpeg',
        lastModified: Date.now(),
      })

      setSelectedImage(URL.createObjectURL(blob))
      stopCamera()
      await handleImageSelect(file)
    } catch (error) {
      console.error('Error capturing:', error)
      setCameraError('Failed to capture image. Please try again.')
    } finally {
      setIsCapturing(false)
    }
  }, [isCameraReady, handleImageSelect, stopCamera])

  const renderSeverityScores = () => {
    if (!analysisResult?.severity_scores) return null

    return Object.entries(analysisResult.severity_scores)
      .sort(([, a], [, b]) => b - a)
      .map(([level, score]) => (
        <div key={level} className="flex items-center gap-2 mb-2">
          <span className="w-24 text-sm font-medium text-gray-700 dark:text-gray-300">{level}:</span>
          <Progress value={score} className="flex-1" />
          <span className="w-16 text-right text-sm text-gray-600 dark:text-gray-400">{formatConfidence(score)}</span>
        </div>
      ))
  }

  const renderCamera = useCallback(
    () => (
      <Card>
        <CardContent className="p-6">
          <div className="relative aspect-video w-full overflow-hidden rounded-lg bg-black mb-4">
            <video
              ref={videoRef}
              autoPlay
              playsInline
              muted
              className="w-full h-full object-cover"
              aria-label="Camera preview"
            />
            {!isCameraReady && (
              <div className="absolute inset-0 flex items-center justify-center bg-black/50">
                <Loader2 className="w-8 h-8 animate-spin text-white" />
                <span className="ml-2 text-white">Starting camera...</span>
              </div>
            )}
            {isCapturing && (
              <div className="absolute inset-0 bg-white/90 flex items-center justify-center">
                <Loader2 className="w-8 h-8 animate-spin text-gray-700" />
                <span className="ml-2 text-gray-700">Capturing...</span>
              </div>
            )}
          </div>

          <div className="flex justify-center gap-4">
            <Button onClick={captureImage} disabled={!isCameraReady || isCapturing} className="w-full">
              {isCapturing ? (
                <>
                  <Loader2 className="w-5 h-5 animate-spin mr-2" />
                  <span>Capturing...</span>
                </>
              ) : (
                <>
                  <Camera className="w-5 h-5 mr-2" />
                  <span>Capture Photo</span>
                </>
              )}
            </Button>

            <Button onClick={stopCamera} variant="outline" className="w-full">
              Cancel
            </Button>
          </div>
        </CardContent>
      </Card>
    ),
    [isCameraReady, isCapturing, captureImage, stopCamera],
  )

  const renderAnalysisResults = () => {
    if (analyzing) {
      return (
        <Card>
          <CardContent className="flex flex-col items-center justify-center p-8">
            <Loader2 className="w-12 h-12 animate-spin text-primary mb-4" />
            <p className="text-gray-600 dark:text-gray-400">Analyzing image...</p>
          </CardContent>
        </Card>
      );
    }

    if (!selectedImage || !analysisResult) {
      return (
        <Card>
          <CardContent className="p-8 text-center">
            <p className="text-gray-600 dark:text-gray-400">{error || "Upload an image to see analysis results"}</p>
          </CardContent>
        </Card>
      );
    }

    if (error) {
      return (
        <Card>
          <CardContent className="p-8 text-center">
            <AlertTriangle className="w-12 h-12 mx-auto mb-4 text-red-500" />
            <p className="text-red-600 dark:text-red-400">{error}</p>
          </CardContent>
        </Card>
      );
    }

    return (
      <Card>
        <CardHeader>
          <CardTitle>Analysis Results</CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="flex items-center justify-between p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
            <span className="text-gray-600 dark:text-gray-400">Severity Level:</span>
            <span className={`px-4 py-2 rounded-full text-sm font-medium ${severityColors[analysisResult.severity]}`}>
              {analysisResult.severity}
            </span>
          </div>

          <div className="p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
            <h3 className="text-lg font-semibold mb-2">Confidence Score</h3>
            <div className="flex items-center">
              <Progress value={analysisResult.confidence} className="flex-1 mr-4" />
              <span className={`text-lg font-bold ${getConfidenceLevel(analysisResult.confidence).color}`}>
                {formatConfidence(analysisResult.confidence)}
              </span>
            </div>
            <p className={`text-sm mt-2 ${getConfidenceLevel(analysisResult.confidence).color}`}>
              {getConfidenceLevel(analysisResult.confidence).message}
            </p>
          </div>

          {analysisResult.severity_scores && (
            <div className="mt-4 pt-4 border-t border-gray-200 dark:border-gray-700">
              <h3 className="text-sm font-semibold text-gray-700 dark:text-gray-300 mb-4 flex items-center gap-2">
                Detailed Scores
                <TooltipProvider>
                  <Tooltip>
                    <TooltipTrigger>
                      <Info className="w-4 h-4 text-gray-400" />
                    </TooltipTrigger>
                    <TooltipContent>Confidence scores for each severity level</TooltipContent>
                  </Tooltip>
                </TooltipProvider>
              </h3>
              <div className="space-y-3">{renderSeverityScores()}</div>
            </div>
          )}

          {analysisResult.confidence < CONFIDENCE_THRESHOLDS.MODERATE && (
            <div className="mt-4 p-4 bg-yellow-50 dark:bg-yellow-900/50 rounded-lg border border-yellow-200 dark:border-yellow-800">
              <div className="flex gap-2 items-start text-yellow-800 dark:text-yellow-200">
                <AlertTriangle className="w-5 h-5 mt-0.5 flex-shrink-0" />
                <div className="text-sm">
                  <p className="font-medium mb-2">Due to the low confidence scores:</p>
                  <ul className="list-disc ml-5 space-y-1">
                    <li>Consult with a healthcare professional for accurate diagnosis</li>
                    <li>Consider retaking the image with better lighting and focus</li>
                    <li>Use this result only as a preliminary screening tool</li>
                    <li>Regular eye examinations are recommended for diabetic patients</li>
                  </ul>
                </div>
              </div>
            </div>
          )}
        </CardContent>
      </Card>
    )
  }

  const renderFileInput = () => (
    <input
      type="file"
      accept={ALLOWED_TYPES.join(',')}
      className="hidden"
      ref={fileInputRef}
      onChange={handleFileChange}
      aria-label="Upload retinal image"
      title="Choose a retinal image file"
    />
  )

  return (
    <div className="min-h-screen p-8 bg-gradient-to-b from-gray-50 to-gray-100 dark:from-gray-900 dark:to-gray-800">
      <main className="max-w-4xl mx-auto">
        <Card className="mb-8">
          <CardHeader>
            <CardTitle className="text-3xl font-bold text-center bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
              Diabetic Retinopathy Detection
            </CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-center text-gray-600 dark:text-gray-400">
              Upload a retinal image for automated DR screening
            </p>
          </CardContent>
        </Card>

        <Card className="mb-8">
          <CardContent className="p-8">
            {isCameraOpen ? (
              renderCamera()
            ) : (
              <div
                className={`border-2 border-dashed rounded-lg p-8 text-center ${
                  error ? "border-red-300" : "border-gray-300 dark:border-gray-700"
                }`}
                onDrop={handleDrop}
                onDragOver={(e) => e.preventDefault()}
              >
                {renderFileInput()}

                {selectedImage ? (
                  <div className="space-y-4">
                    <div className="relative h-64 w-full">
                      <Image
                        src={selectedImage || "/placeholder.svg"}
                        alt="Selected retinal image"
                        fill
                        className="object-contain rounded-lg"
                      />
                    </div>
                    <Button
                      onClick={() => fileInputRef.current?.click()}
                      disabled={uploading || analyzing}
                      className="w-full"
                    >
                      {uploading ? (
                        <>
                          <Loader2 className="w-5 h-5 animate-spin mr-2" />
                          <span>Processing...</span>
                        </>
                      ) : (
                        <>
                          <Camera className="w-5 h-5 mr-2" />
                          <span>Take Another Photo</span>
                        </>
                      )}
                    </Button>
                  </div>
                ) : (
                  <div className="space-y-4">
                    <Upload className="w-12 h-12 mx-auto text-gray-400" />
                    <div>
                      <p className="text-gray-600 dark:text-gray-400">Take a photo or upload a retinal image</p>
                      <p className="text-sm text-gray-500 dark:text-gray-500 mt-2">
                        Supported formats: JPEG, PNG (max 10MB)
                      </p>
                    </div>
                    <div className="flex justify-center gap-4">
                      <Button onClick={() => fileInputRef.current?.click()} variant="outline">
                        <Upload className="w-5 h-5 mr-2" />
                        <span>Select Image</span>
                      </Button>
                      <Button
                        onClick={() => {
                          setIsCameraOpen(true)
                          startCamera()
                        }}
                      >
                        <Camera className="w-5 h-5 mr-2" />
                        <span>Use Camera</span>
                      </Button>
                    </div>
                  </div>
                )}
              </div>
            )}

            {cameraError && (
              <div className="mt-4 p-4 bg-red-50 dark:bg-red-900/50 border border-red-200 dark:border-red-800 rounded-lg">
                <div className="flex items-center gap-2 text-red-700 dark:text-red-200">
                  <AlertTriangle className="w-5 h-5" />
                  <p>{cameraError}</p>
                </div>
              </div>
            )}

            {error && (
              <div className="mt-4 p-4 bg-red-50 dark:bg-red-900/50 border border-red-200 dark:border-red-800 rounded-lg">
                <div className="flex items-center gap-2 text-red-700 dark:text-red-200">
                  <AlertTriangle className="w-5 h-5" />
                  <p>{error}</p>
                </div>
              </div>
            )}
          </CardContent>
        </Card>

        {renderAnalysisResults()}

        {analysisHistory.length > 0 && (
          <Card className="mt-8">
            <CardHeader>
              <CardTitle>Analysis History</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
                {analysisHistory.map((result) => (
                  <Card key={result.id} className="overflow-hidden">
                    <CardHeader className="p-4">
                      <div className="flex justify-between items-center">
                        <span className={`px-3 py-1 rounded-full text-sm font-medium ${severityColors[result.severity]}`}>
                          {result.severity}
                        </span>
                        <time className="text-sm text-gray-500 dark:text-gray-400">
                          {new Date(result.date).toLocaleDateString()}
                        </time>
                      </div>
                    </CardHeader>
                    <CardContent className="p-4">
                      <Progress value={result.confidence} className="mb-2" />
                      <p className={`text-sm ${getConfidenceLevel(result.confidence).color}`}>
                        Confidence: {formatConfidence(result.confidence)}
                      </p>
                    </CardContent>
                  </Card>
                ))}
              </div>
            </CardContent>
          </Card>
        )}
      </main>
    </div>
  )
}

export default Dashboard

